<template>
    <div class="appBox">
        <template v-if="list.length > 1" v-for="item in list">
            <van-cell :title="item.title" is-link :to="'/noticeinfo?id='+item.id"/>
        </template>
        <template v-else-if="list.length == 1">
            <div class="appcontent" style="padding: 0.1rem" v-html="list[0]['content']">

            </div>
        </template>
    </div>
</template>

<script>
    import { mRule } from "@/api/user"
    import Toast from 'vant/lib/toast'
    import 'vant/lib/toast/style'
    import Dialog from 'vant/lib/dialog'
    import 'vant/lib/dialog/style'
    import Cell from 'vant/lib/Cell'
    import 'vant/lib/cell/style'


    export default {
        name: "Mrule",
        components: {
            [Cell.name]: Cell,
        },
        data()
        {
            return {
                list:[]
            }
        },
        mounted()
        {
           this.getList()
        },
        methods:{

            getList()
            {
                mRule({type:2}).then(res => {
                    this.list = res
                })
            }
        }
    }
</script>

<style scoped>
    .appcontent div p {
        font-size: 0.13rem;
        color: #333333;
    }
    .appcontent div img {
        width: 3rem;
        padding: .1rem 0;
        display: inline-block;
        border-radius: .04rem;
        margin: auto;
    }
</style>



